  }
}
.nav--closed .nav__toggle::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 18px;
  background-image: url("../img/nav-toggle.svg");
  top: 0;
  right: 0;
}
.nav--opened .nav__toggle::before,
.nav--opened .nav__toggle::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: #000000;
  right: 0;
}
.nav--opened .nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.nav--opened .nav__toggle::after {
  transform: rotate(-45deg);
}
