.page {
  height: 100%;
}

.page__body {
  min-width: 320px;
  margin: 0;
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: @base-text-color;
  background-color: @white;
  overflow-x: hidden;
}
