.input,
.textarea {
  &:hover {
    border-color: @another-grey;
  }
}

.input--email:hover + .program-selection-form__item--email::after {
  background-color: @green;
}

.input:invalid:not(:placeholder-shown) {
  outline: 2px solid @error;
}

/*     input -   input  .error */

.error {
  outline: 2px solid @error;
}
